* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "IBM Plex Sans", sans-serif;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

/* page hidden for SPA */
.page {
  display: none;
  animation: fadeIn 0.3s;
}

.wrapper {
  width: 100%;
  display: grid;
  min-height: 100vh;
  grid-template-rows: 192px auto 64px;
  grid-template-columns: 180px auto;
  grid-template-areas: "topbar topbar" "content content" "navbar navbar";
}

main {
  background-color: #e6e6e6;
  grid-area: content;
}

.topbar {
  grid-area: topbar;
}

.navbar {
  grid-area: navbar;
}

.nav-links {
  background-color: white;
  display: flex;
  gap: 20px;
  justify-content: center;
}
.nav-links a {
  color: black;
  text-decoration: none;
  font-size: 18px;
}
.nav-links a:hover {
  background-color: black;
  color: white;
}
.nav-links a.active {
  border-bottom: 2px solid black;
}

.cards-list {
  width: 100%;
  min-width: 280px;
}

.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 1px solid #e69537;
  border-radius: 16px;
  padding: 16px;
  box-shadow: 6px 6px #e69537;
}
.card-top {
  font-family: "IBM Plex Mono", monospace;
  font-size: 10px;
  line-height: 16px;
  font-weight: 500;
  text-rendering: optimizeLegibility;
  display: flex;
}